// 在此进行请求和响应拦截
import axios from 'axios'

// 超时时间
axios.defaults.timeout = 10000


// 响应拦截
axios.interceptors.response.use(res => {
  // 响应成功
  return res.data
}, err => {
  // 响应失败
  Message.error('加载失败!')
  return Promise.reject(err)
})

export default axios


